* {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  box-sizing: border-box;
}
body {
  font-family: "Arial", sans-serif !important;
}

button {
  cursor: pointer;
}

.todo {
  margin: 5px;
  max-width: 600px;
  background: rgb(59, 59, 59);
  margin: 5px auto;
  color: #fff;
  border-radius: 3px;
  box-shadow: 1px 2px 10px rgb(134, 134, 134);
  padding: 15px 15px;
  transition: 0.3s all;
  cursor: pointer;
}

.todo:hover {
  background: rgb(88, 87, 87);
}

.todo p {
  font-size: 24px;
  padding: 10px 5px;
}

button {
  border: none;
  color: white;
  padding: 8px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  margin: 0 5px;
}
input[type="text"] {
  padding: 8px 10px;
  font-size: 14px;
  border: 1px solid rgb(59, 59, 59);
}
button:hover {
  opacity: 0.8;
}
.btn-danger {
  background: #f44336;
}
.btn-success {
  background: #4caf50;
}
.todo-done {
  text-decoration: line-through;
}
.sort-input {
  display: block;
  border: 1px solid rgb(59, 59, 59);
  padding: 7px 1px;
  margin: 20px auto;
}

.add-container {
  padding: 20px;
  padding-bottom: 0;
  display: flex;
  justify-content: center;
}
